export default {
	data() {
		return {
			// 手机号码	
			phone: '',
			//  获取验证码哪里的文字
			gain: '获取验证码',
			//  用户输入的验证码
			verification: '',
			//  用来记时间的定时器
			setinter: '',
			code:{},
		};
	},
	onLoad() {
		this.code_updara().then(dd =>{
			// console.log(dd)
			this.code = dd;
		})
	},
	methods: {
		// 调用code
		code_updara(){
			return new Promise((rejk, res) => {
				uni.login({
					success: function(e) {
						rejk(e);
					}
				});
			});
		},
		/* 游客登录*/
		getUserInfo_yk() {
			var userinfo = { avatarUrl: '../../static/My/logo.jpg', nickName: '游客' ,user_pone_Br:true};
			this.$store.commit('userinfoMutations', userinfo)
			uni.switchTab({
				url: '../../pages/My/My'
			})
			uni.setStorage({ /* 存在本地方便 以后取*/   /* 用户权限*/
				key: 'is_program_user',
				data: 1,
				success: (e) => {}
			})
			return false;
		},

		//登录
		getUserInfo_data(e) {
			let  code = this.code;

				this.$store.dispatch({type:'userinfo_actions',e,code})



		},
		
		// 用户点击登录之后调用的接口
		register() {
			uni.getUserInfo({
				provider: 'weixin',
				success: function(infoRes) {
					console.log('用户昵称为：' + infoRes.userInfo.nickName);
					console.log(infoRes);
				}
			});
		}
	}
};
